<template>
    <div class="main">
        <div class="deckArrangement">
            <el-button @click.prevent="banpaiAdd" type="primary" size="small" style="margin:20px 10px;">新增预警灯配置</el-button>
            <div>
                已选设备
            </div>

            <!-- 表单 -->
            <div class="home_table">
                <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" align="center" :label="item.title" :width="item.width" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="120">
                        <template slot-scope="scope">
                            <!-- <el-button @click="handleEit(scope.row)" type="text" size="small">编辑</el-button> -->
                            <el-button @click="handleClickDel(scope.row.id)" type="text" style="color: #ff2b2b" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 分页 -->
            <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<10? true:false" />
            </div>
            <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" @close="dialogVisible=false" :close-on-click-modal="false">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="设备选择">
                        <el-button @click="screenClick">设备选择</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
                        <!-- <el-button @click="resetForm('ruleForm')">取 消</el-button> -->
                    </el-form-item>
                </el-form>
            </el-dialog>
            <el-dialog title="选择设备" :visible.sync="screenVisible" width="30%" @close="screenVisible=false" :close-on-click-modal="false">
                <el-form>
                    <el-form-item style="display: block;">
                        <el-checkbox v-model="isSelectAll" @change="selectAll">全选/全不选</el-checkbox>
                        <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="device_id" ref="tree" :props="defaultProps">
                        </el-tree>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="screenSubmit">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import {
    drillView,
    deckListApi,
    deckArrangementDel,
    deckArrangementInfo,
    deckArrangementAdd,
    deckArrangementEditApi,
    switchMentList,
    switchMentInfo,
    switchMentAdd,
    switchMentEdit,
    switchMentDel,
    hashApi
} from "@/api/index"
export default {
    name: "deckArrangement",
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 10,
            rehearsal_id: "",
            tableData: [],
            tableHeight: window.innerHeight - 180,
            tableHead: [
                { title: '序号', prop: 'id', width: '90' },
                { title: '设备名称', prop: 'device_name', minWidth: '120', },
                { title: '设备所在位置', prop: 'location_name', minWidth: '120', },
            ],

            disabled: false,
            title: '',
            hash: "",
            //弹层数据
            centerDialogVisible: false,
            centerDialogVisibleEit: false,
            ruleForm: {
                name: '',
                region: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
            },
            value: [],
            //编辑图片按钮
            fileList: [],
            centerDialogVisiblePhotoImg: false,
            multipleSelection: [],
            baseUrl: "",
            dialogVisible: false,
            showTypeList: [],//素材类型列表
            showTitle: '选择图片',//素材选择title
            previewData: [],
            showTypeVisible: false,//素材选择弹窗
            keyword: '',
            showNodeData: [],//素材表格列表
            screen: [],
            screenVisible: false,
            // tree是否全选，复选框默认状态（）
            isSelectAll: false,
            nodeData: [],//设备树状列表
            defaultProps: {
                children: 'children',
                label: 'device_name',
            },
            tableHeadCopy: [],
        }
    },

    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        if (localStorage.getItem('Rehearsal_Id')) {
            this.getlist();
        }
        this.resView()
        this.getHash();
        this.tableHeadCopy = JSON.parse(JSON.stringify(this.tableHead));
    },

    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getlist();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getlist();
        },
        backDrill() {
            this.$router.push({ name: "drillManagement" })
        },
        selectAll() {
            if (this.isSelectAll) {
                // 	设置目前勾选的节点，使用此方法必须设置 node-key 属性
                this.$refs.tree.setCheckedNodes(this.nodeData);
            } else {
                // 全部不选中
                this.$refs.tree.setCheckedNodes([])
            }
        },
        //设备选择
        screenClick() {
            this.resView();
            this.screenVisible = true;
            this.isSelectAll = false;
            this.$nextTick(() => {
                if (this.screen.length > 0) {
                    this.getNodeData(this.screen);
                }
            })
        },
        ///提交
        screenSubmit() {
            this.screen = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());
            this.screenVisible = false;
        },
        async getInfo(id) {
            const res = await this.axios.post(switchMentInfo, { id: id });
            if (res.data.code == 1) {
                this.ruleForm = res.data.data;
                this.device_ids = res.data.data.list
                this.screen = res.data.data.list
                this.resView()
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            let data = {
                device_type: '6',
                // curr: this.page,
                // pageSize: this.pageSize,
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data);
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                this.showNodeData = Object.prototype.toString.call(res.data.data.file_list) === '[object Object]' ? res.data.data.file_list.data : [];
                // this.total = res.data.data.file_list.total;
                this.showTypeList = res.data.data.show_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getNodeData(e) {
            this.nodeData = [];
            let data = {
                device_type: '6',
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data)
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                if (e) {
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        e.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else {
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.device_id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.device_id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //获取列表
        async getlist() {
            let data = {
                rehearsal_id: JSON.parse(localStorage.getItem('Rehearsal_Id')),
                curr: this.page,
                pageSize: this.pageSize,
            }
            const res = await this.axios.post(switchMentList, data);
            if (res.data.code == 1) {
                this.total = res.data.data.total
                this.tableData = res.data.data.data
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                })
            }
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'switchMent' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },

        //表单重置
        resetForm() {
            this.ruleForm = {
                scene_name: '',
                title: "",
            };
            this.$nextTick(() => {
                this.$refs['ruleForm'].resetFields();
            })
        },

        //班牌新增
        banpaiAdd() {
            // this.$router.push({ name: "deckArrangementAdd" })
            this.title = '新增预警灯配置';
            this.dialogVisible = true;
            this.resetForm();

        },
        //编辑按钮
        handleEit(row) {
            // this.$router.push({
            //     name: "deckArrangementAdd", params: {
            //         id: row.id,
            //     }
            // })
            this.title = '编辑预警灯配置';
            this.infoId = row.id;
            this.dialogVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.getInfo(row.id)
            })
        },

        handleClickDel(id) {
            this.$confirm('是否确定进行删除', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(switchMentDel, datas)
                        .then(res => {
                            if (res.data.code == 1) {
                                this.$notify.success({
                                    showClose: true,
                                    message: res.data.msg,
                                    type: 'success'
                                });
                                if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                    this.page -= 1;
                                }
                                this.getlist();
                            } else {
                                this.$notify.error({
                                    title: '失败',
                                    message: res.data.msg
                                });
                            }
                        });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.error({
                    //     title: '失败',
                    //     message: '取消操作'
                    // });
                });
        },
        //表单提交
        submitForm() {
            this.$refs['ruleForm'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let numReg = /^[0-9]*$/
                    let numRe = new RegExp(numReg)
                    let arr = [];
                    this.screen.forEach(e => {
                        if (numRe.test(e)) {
                            arr.push(e);
                        }
                    })
                    let data = {
                        rehearsal_id: JSON.parse(localStorage.getItem("Rehearsal_Id")),
                        device_ids: arr.join(','),
                        hash: this.hash,
                    };
                    let api;
                    if (typeof this.infoId != 'undefined') {
                        api = switchMentEdit;
                        data.id = this.infoId;
                    } else {
                        api = switchMentAdd;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getlist();
                    this.getHash();
                }
            })
        },
    }

}
</script>

<style lang="scss" scoped>
.deckArrangement {
    //编辑配置的图片样式
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 150px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n + 1) {
        background-color: #d3dce6;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>


